:root {
  --accent: #607ce6;
  --dark: #333;
  --light: #fff;
  --third: #d9d9d9;
  --gray: #d8d7d7;
  --light-gray: #f5f5f5;
  --font-family: "Source Sans 3", sans-serif;
  --content-width: 1180px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --mb: 32px;
  --pb: 80px;
  --gap: 20px;
}

